input, textarea, input:focus, textarea:focus {
  border: 1px solid #cccccc;
  outline: none; }

@-webkit-keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0); }

  40% {
    transform: translateY(-30px); }

  60% {
    transform: translateY(-15px); } }

.home-page {
  background: #6C7A87; }
  .home-page .landing {
    width: 100%;
    height: 100%; }
    .home-page .landing .logo img {
      width: 100px;
      margin-top: 20px;
      margin-left: 20px; }
    .home-page .landing .catch-phrase-container {
      top: 15%;
      position: absolute;
      width: 100%; }
      .home-page .landing .catch-phrase-container .catch-phrase {
        font-family: BIG_JOHN;
        display: inline-block;
        position: relative;
        left: 50%;
        -webkit-transform: translateX(-50%);
        -moz-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
        transform: translateX(-50%);
        color: #EAEAEA; }
    .home-page .landing .landing-title-container {
      position: relative;
      width: 100%;
      font-family: "Roboto Slab", "Roboto", Helvetica, Arial, sans-serif;
      color: #333333;
      font-size: 96px;
      font-size: 6rem;
      line-height: 192px;
      line-height: 12rem;
      top: 15%;
      font-family: Slim_Joe; }
      .home-page .landing .landing-title-container .landing-title {
        display: inline-block;
        left: 50%;
        -webkit-transform: translateX(-45%);
        -moz-transform: translateX(-45%);
        -ms-transform: translateX(-45%);
        transform: translateX(-45%); }
        .home-page .landing .landing-title-container .landing-title .web-title {
          margin-right: 5px;
          color: #F6CA1B; }
        .home-page .landing .landing-title-container .landing-title .gold-title {
          font-family: BIG_JOHN;
          margin-left: 5px;
          color: #0092BF; }
    .home-page .landing .vert-line-container {
      position: absolute;
      width: 100%;
      top: 18%;
      height: 100%; }
      .home-page .landing .vert-line-container .vert-line {
        display: inline-block;
        position: relative;
        left: 50%;
        -webkit-transform: translateX(-50%);
        -moz-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
        transform: translateX(-50%);
        border-left: thin solid #EBEBEB;
        width: 2px;
        height: 62%; }
    .home-page .landing .bar {
      position: fixed; }
    .home-page .landing .bounce {
      position: fixed;
      left: 50%;
      bottom: 0;
      margin-top: -25px;
      margin-left: -25px;
      height: 50px;
      width: 50px;
      background: red;
      -webkit-animation: bounce 2s infinite; }

